<template>
	<el-card style="
			margin: 10px;
			margin-top: 10px;
			min-width: 450px; /* height: 90vh*/
		">
		<div style="margin-bottom: 20px;">
			<el-button size="small" @click="addTab('今日数据')">
				今日数据
			</el-button>
			<el-button size="small" @click="addTab('昨日数据')">
				昨日数据
			</el-button>
			<el-button size="small" @click="addTab('全年数据')">
				全年数据
			</el-button>
			<el-button size="small" @click="addTab('全部数据')">
				全部数据
			</el-button>
		</div>
		<el-tabs v-model="editableTabsValue" type="card" closable @tab-remove="removeTab">
			<el-tab-pane v-for="(item, index) in editableTabs" :key="item.name" :label="item.title" :name="item.name">
				<!-- {{ item.content }} -->
				<el-empty description="echarts缺省"></el-empty>
			</el-tab-pane>
		</el-tabs>
	</el-card>
</template>

<script>
export default {
	data() {
		return {
			editableTabsValue: '2',
			editableTabs: [{
				title: '今日数据',
				name: '1',
				content: 'Tab 1 content'
			}, {
				title: '昨日数据',
				name: '2',
				content: 'Tab 2 content'
			}],
			tabIndex: 2
		}
	},
	methods: {
		addTab(targetName) {
			let newTabName = ++this.tabIndex + '';
			this.editableTabs.push({
				title: targetName,
				name: newTabName,
				content: 'New Tab content'
			});
			this.editableTabsValue = newTabName;
		},
		removeTab(targetName) {
			let tabs = this.editableTabs;
			let activeName = this.editableTabsValue;
			if (activeName === targetName) {
				tabs.forEach((tab, index) => {
					if (tab.name === targetName) {
						let nextTab = tabs[index + 1] || tabs[index - 1];
						if (nextTab) {
							activeName = nextTab.name;
						}
					}
				});
			}

			this.editableTabsValue = activeName;
			this.editableTabs = tabs.filter(tab => tab.name !== targetName);
		}
	}
}
</script>