import { ref, inject, onMounted } from 'vue';
const centerDialogVisible = ref(false);

// 获取内容
const api = inject('api');
const GetEssay = async () => {
	try {
		const res = await api.GetTheEssay();
		tableData.value = res.data.essays;
		// console.log(tableData);
	} catch (error) {
		ElNotification({
			title: '提示',
			message: '接口未上线，展示本地数据',
			type: 'warning', // 使用警告类型的通知
		});
	}
};
onMounted(() => {
	GetEssay();
});

